*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.container{

    width: 100%;
    height: 100vh;

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(28, 164, 112);
}


.calculator{
    padding: 20px;
    border-radius: 10px;
    background-color: white;
}


form input{
    border: none;
    outline: 0;
    width: 60px;
    height: 60px;
    font-size: 16px;
    background-color: chartreuse ;
    margin: 2px;
    border-radius: 10px;
    color: white;
    font-weight: bolder;
    cursor: pointer;
}


form input[type="button"]:hover{
    background-color: rgb(64, 113, 14) ;
}

form .display{
    display: flex;
    justify-content: flex-end;
    margin:5px 0px 20px 0px;
}


form .display input{
    text-align: right;
    flex: 1;
    font-size: 40;
    padding: 5px 10px;
    background-color: rgb(153, 141, 141);
}


form input.equal{
    width: 125px;
}